import * as React from "react";
import { makeStyles, Theme } from "@material-ui/core/styles";

export const DumbOverlayContainer: React.FC<{
  children: React.ReactNode;
}> = props => {
  const styles = withStyles();

  return (
    <div className={styles.absoluteRoot}>
      <div className={styles.relativeRoot}>{props.children}</div>
    </div>
  );
};

const withStyles = makeStyles((theme: Theme) => ({
  absoluteRoot: {
    position: "absolute",
    top: 0,
    left: 0,
    height: "100vh",
    width: "100vw"
  },
  relativeRoot: {
    position: "relative",
    height: "100vh",
    width: "100vw",
    zIndex: 2,
    pointerEvents: 'none',
  }
}));
