import React from "react";
import "./App.css";
import { Switch, Route, Redirect } from "react-router-dom";
import { IiotContextProviders } from "./IiotAppProviders";
import { BarView } from "./3d/BarView";
import { Overlay } from "./overlay/Overlay";

function App() {
  return (
    <IiotContextProviders>
      <Switch>
        <Redirect exact from="/" to={"/dash"} />
        <Route path="/dash">
          <BarView />
          <Overlay />
        </Route>
      </Switch>
    </IiotContextProviders>
  );
}

export default App;
