import { IiotThunkActionCreator } from "../../store";
import { barViewActions } from "./barViewReducer";
import { InstallationStructure } from "../utility/installationStructure";

function fetchInstallationStructure(): Promise<InstallationStructure> {
  // return fetch(`api/installation-structure`).then(data => data.json());
  return Promise.resolve({ x: 4, y: 5 });
}

export const retrieveInstallationStructureThunk: IiotThunkActionCreator = () => dispatch => {
  return fetchInstallationStructure().then(structure => {
    dispatch(barViewActions.setInstallationStructure(structure));
  });
};
