import * as React from "react";
import { makeStyles, Theme } from "@material-ui/core/styles";
import { useDispatch, useSelector } from "react-redux";
import { IiotStoreState } from "../../rootReducer";
import { TwoDCoord } from "../../utility/types";
import { IconButton, Typography } from "@material-ui/core";
import { ChevronLeft, ChevronRight } from "@material-ui/icons";
import { useCallback, useMemo } from "react";
import { barViewActions } from "../../3d/data/barViewReducer";
import dayjs from "dayjs";

// @ts-ignore
function pad(n, width) {
  n = n + "";
  return n.length >= width ? n : new Array(width - n.length + 1).join("0") + n;
}
function floaterId(position: TwoDCoord) {
  return `HF${pad(position.y * 4 + position.x, 2)}`;
}

export const SelectedInfo = () => {
  const dispatch = useDispatch();
  const nextFloater = useCallback(
    () => dispatch(barViewActions.selectNextFloater()),
    [dispatch]
  );
  const previousFloater = useCallback(
    () => dispatch(barViewActions.selectPreviousFloater()),
    [dispatch]
  );
  const selectedFloaterCoordinate = useSelector(
    (state: IiotStoreState) => state.barView.selectedFloaterCoordinate
  );
  const styles = withStyles();
  const floaterInfo: FloaterInfo = useMemo(() => {
    if (selectedFloaterCoordinate === null) {
      return null as any;
    }
    return getFloaterInfo(selectedFloaterCoordinate);
  }, [selectedFloaterCoordinate]);

  return (
    <div className={styles.root}>
      <div className={styles.titleArea}>
        {selectedFloaterCoordinate !== null && (
          <IconButton
            classes={{ root: styles.chevron }}
            onClick={previousFloater}
          >
            <ChevronLeft />
          </IconButton>
        )}
        <Typography variant={"h4"} classes={{ root: styles.heading }}>
          {selectedFloaterCoordinate !== null
            ? `Floater ${floaterId(selectedFloaterCoordinate)}`
            : "Select a floater..."}
        </Typography>
        {selectedFloaterCoordinate !== null && (
          <IconButton classes={{ root: styles.chevron }} onClick={nextFloater}>
            <ChevronRight />
          </IconButton>
        )}
      </div>
      {selectedFloaterCoordinate !== null && (
        <div className={styles.grid}>
          <Typography variant={"subtitle1"}>Position</Typography>
          <Typography variant={"body1"}>
            {`Column ${selectedFloaterCoordinate?.y} Row ${selectedFloaterCoordinate?.x}`}
          </Typography>
          <Typography variant={"subtitle1"}>Status</Typography>
          <Typography
            variant={"body1"}
            className={
              floaterInfo.status === "End of life approaches"
                ? styles.error
                : ""
            }
          >
            {floaterInfo.status}
          </Typography>

          <Typography variant={"subtitle1"}>Avg production [W]</Typography>
          <Typography variant={"body1"}>
            {floaterInfo.averageProduction}
          </Typography>

          <Typography variant={"subtitle1"}>Avg tension [Nm]</Typography>
          <Typography variant={"body1"}>
            {floaterInfo.averageTension}
          </Typography>

          <Typography variant={"subtitle1"}>Avg sun obstruction [%]</Typography>
          <Typography variant={"body1"}>
            {floaterInfo.averageSunObstruction}
          </Typography>

          <Typography variant={"subtitle1"}>Messages</Typography>
          <div>
            {floaterInfo.messages.map(message => (
              <Typography variant={"body1"}>
                {`${dayjs(message.time).format("YYYY-MM-DD HH:MM")} ${
                  message.message
                }`}
              </Typography>
            ))}
          </div>
        </div>
      )}
    </div>
  );
};

function getFloaterInfo(coord: TwoDCoord) {
  return floaterInfoList[(coord.y * 4 + coord.x) % 5];
}

