import * as React from "react";
import { Floater } from "./Floater";
import { DIMENSIONS } from "./data/dimensions";
import { useCallback, useEffect, useMemo, useState } from "react";
import { Coords } from "./utility/coords";
import * as R from "ramda";
import { useDispatch } from "react-redux";
import { barViewActions } from "./data/barViewReducer";
import { useFloaterContainerSelected } from "./useFloaterContainerSelected";

export const FloaterContainer: React.FC<{
  gridPosition: { x: number; y: number };
}> = props => {
  const selected = useFloaterContainerSelected(props.gridPosition);
  // const state = useContext(BarViewStateContext);
  const position = useMemo<Coords>(
    () => [
      DIMENSIONS.TRAY_WIDTH / 2 +
        props.gridPosition.y *
          (DIMENSIONS.TRAY_WIDTH + DIMENSIONS.TRAY_CLEARANCE),
      0,
      DIMENSIONS.TRAY_WIDTH / 2 +
        props.gridPosition.x *
          (DIMENSIONS.TRAY_WIDTH + DIMENSIONS.TRAY_CLEARANCE)
    ],
    [props.gridPosition]
  );
  const [sequenceOfNumbers, setNumber] = useState(0);
  useEffect(() => {
    R.pipe(
      R.map(() => [4, 0, 3, 0]),
      R.unnest
    )(R.range(0, 100)).forEach((value, index) => {
      setTimeout(() => {
        setNumber(value);
      }, index * 3000);
    });
  }, []);
  const dispatch = useDispatch();
  const onClick = useCallback(() => {
    return dispatch(barViewActions.setSelectedCoordinate(props.gridPosition));
  }, [dispatch, props.gridPosition]);
  // const delay = useMemo(
  //   () =>
  //     200 * (state.installationStructure.x - props.gridPosition.x) +
  //     300 * (state.installationStructure.y - props.gridPosition.y),
  //   []
  // );
  return (
    <>
      <Floater
        onClick={onClick}
        position={useMemo(() => [position[0], sequenceOfNumbers, position[2]], [
          sequenceOfNumbers,
          position
        ])}
        motionPeriod={3}
        selected={selected}
      />
    </>
  );
};
