import * as R from "ramda";
// @ts-ignore
import { easeQuadInOut } from "d3-ease";

export const springConfig = { mass: 1, tension: 170, friction: 26 };
export const springConfigWithPeriod = R.memoizeWith(
  R.identity as any,
  (period: number) => {
    const tension =
      (Math.pow(Math.PI, 2) * (springConfig.mass as any)) /
      Math.pow(period * 3, 2);
    const config = {
      ...springConfig,
      tension,
      friction: (20 / 17) * tension
    };
    console.log("üèû", "config is", config);
    return config;
  }
);
export const sprindDurationConfigWithPeriod = R.memoizeWith(
  R.identity as any,
  (period: number) => {
    const config = {
      duration: period * 1000.0,
      easing: easeQuadInOut
    };
    console.log("üèû", "config is", config);
    return config;
  }
);
