import { useCallback, useState } from "react";

export interface Size {
  x: number;
  y: number;
}

/**
 * state for ReactResizeDetector
 *
 * @example
 * const [size, setSize] = useSize();
 * ...
 * <ReactResizeDetector  onResize={setSize}
 *   handleHeight handleWidth
 * />
 *
 */
export const useSize = () => {
  const [size, _setSize] = useState<Size>({ x: 0, y: 0 });
  const setSize = useCallback(
    (x, y) => {
      _setSize({ x, y });
    },
    [_setSize]
  );
  return [size, setSize] as [Size, (width: number, height: number) => void];
};
