export const Floater: React.FC<{
  position: [number, number, number];
  motionPeriod: number;
  onClick?: () => void;
  selected: boolean;
}> = props => {
  const state = useContext(BarViewStateContext);
  const group = useMemo(() => {
    if (state.floaterMesh === undefined) {
      throw new Error("Floater model is not ready");
    }
    const newMesh = state.floaterMesh.clone();
    const group = new THREE.Group();
    group.add(newMesh);
    return group;
  }, [state.floaterMesh]);

  // this hack fixes a problem with "resetting" spring
  // https://github.com/react-spring/react-spring/issues/366#issuecomment-446328238
  const [springState, setSpringState] = useState({
    pos: props.position,
    from: {
      pos: props.position
    },
    config: sprindDurationConfigWithPeriod(props.motionPeriod)
  });
  const spring = useSpring({
    ...springState
  });
  const previousProps = usePrevious(props);
  useEffect(() => {
    setSpringState({
      pos: props.position,
      from: {
        pos:
          previousProps !== undefined ? previousProps.position : props.position
      },
      config: sprindDurationConfigWithPeriod(props.motionPeriod)
    });
  }, [props.position, props.motionPeriod]); // eslint-disable-line react-hooks/exhaustive-deps

  const selectionRef = useRef<any>();
  useEffect(() => {
    if (props.selected) {
      const geo = new THREE.BoxGeometry(3.2, 0.3, 3.2);
      const mat = new THREE.MeshPhongMaterial({
        color: 0x1070e4,
        opacity: 0.5,
        transparent: true
      });
      const mesh = new THREE.Mesh(geo, mat);
      selectionRef.current = mesh;
      group.add(mesh);
    } else {
      group.remove(selectionRef.current as any);
    }
  }, [props.selected, group]);

  return useMemo(
    () =>
      group ? (
        <>
          <animated.primitive
            onClick={props.onClick}
            object={group}
            position={spring.pos}
          />
        </>
      ) : null,
    [spring.pos, group, props.onClick]
  );
};
