import { useEffect, useMemo } from "react";
import { DIMENSIONS } from "./data/dimensions";
import { PerspectiveCamera, Vector3 } from "three";
import { useThree } from "react-three-fiber";
import { InstallationStructure } from "./utility/installationStructure";

export const usePointCamera = (
  installationStructure: InstallationStructure
) => {
  const three = useThree();
  const bounding = useMemo(
    () => [
      DIMENSIONS.TRAY_WIDTH +
        (installationStructure.y - 1) *
          (DIMENSIONS.TRAY_WIDTH + DIMENSIONS.TRAY_CLEARANCE),
      0,
      DIMENSIONS.TRAY_WIDTH +
        (installationStructure.x - 1) *
          (DIMENSIONS.TRAY_WIDTH + DIMENSIONS.TRAY_CLEARANCE)
    ],
    [installationStructure.y, installationStructure.x]
  );
  useEffect(() => {
    const camera: PerspectiveCamera = three.camera as PerspectiveCamera;
    // const distance = bounding[0] / 2 / Math.tan(75 / 2 / (180 / Math.PI));
    // camera.position.z = bounding[2] * (installationStructure.x > 4 ? 1.2 : 1);
    camera.position.x = 10;
    camera.position.y = 15;
    camera.position.z = 24;
    // camera.position.y = distance;
    // camera.position.z = camera.position.z + 6;
    // camera.position.y = camera.position.y + 2;
    // camera.position.x = bounding[0] / 2;
    camera.lookAt(new Vector3(bounding[0] / 2, 0, bounding[2] / 3 + 4));
    // camera.lookAt(0, 0,0);
    console.log("üì∑", "position", camera.position, "where to point", bounding);
  }, [bounding, three.camera, installationStructure]);
};
