import { useContext, useEffect, useState } from "react";
import { FBXLoader } from "three/examples/jsm/loaders/FBXLoader";
import { Mesh, MeshPhongMaterial, RepeatWrapping, Texture } from "three";
import { TGALoader } from "three/examples/jsm/loaders/TGALoader";
import { BarViewStateContext } from "./BarViewScene";

/**
 * What to scale the panel with so that the dimensions are 3x3
 */
const SCALE = 3.0 / 225;

export const useLoadFloaterModel = () => {
  const [floaterModel, setFloaterModel] = useState<Mesh>();
  const state = useContext(BarViewStateContext);
  useEffect(() => {
    loadFloaterModel().then(floaterMesh => {
      state.floaterMesh = floaterMesh;
      setFloaterModel(floaterMesh);
    });
  }, []); // eslint-disable-line react-hooks/exhaustive-deps
  return floaterModel;
};

function loadFloaterModel() {
  return Promise.all([loadFloaterPanelMesh(), loadFloaterPanelTexture()]).then(
    ([floaterMesh, floaterTexture]: [Mesh, Texture]) => {
      const materials = floaterMesh.material as [
        MeshPhongMaterial,
        MeshPhongMaterial
      ];
      if (materials[0].name !== "Body" || materials[1].name !== "Solcelle") {
        throw new Error("Unexpected loading result wrt materials");
      }
      floaterTexture.wrapS = RepeatWrapping;
      floaterTexture.wrapT = RepeatWrapping;
      materials[0].color.setHex(0x2b2d2f);
      materials[1].color.setHex(0xffffff);
      materials[1].emissive.setHex(0x000000);
      materials[1].needsUpdate = true;
      materials[1].map = floaterTexture;
      floaterMesh.geometry.scale(SCALE, SCALE, SCALE);
      console.log("‚ÑπÔ∏è", "floaterMesh", floaterMesh);
      return floaterMesh;
    }
  );
}

function loadFloaterPanelMesh() {
  const loader = new FBXLoader();
  return new Promise<Mesh>((resolve, reject) => {
    loader.load(process.env.PUBLIC_URL + "/res/panel.fbx", panelLoadResult => {
      panelLoadResult.traverse(loadedObject => {
        if (loadedObject.type !== "Mesh") {
          return;
        }
        if (loadedObject.name === "Panel") {
          resolve(loadedObject as Mesh);
        } else {
          reject("Unexpected loading result");
        }
      });
    });
  });
}

function loadFloaterPanelTexture() {
  const textureLoader = new TGALoader();
  return new Promise<Texture>(resolve => {
    textureLoader.load(process.env.PUBLIC_URL + "/res/panel-texture.tga", texture => {
      resolve(texture);
    });
  });
}
