import { TwoDCoord } from "../utility/types";
import { useSelector } from "react-redux";
import { IiotStoreState } from "../rootReducer";

export const useFloaterContainerSelected = (gridPosition: TwoDCoord) => {
  const selectedFloaterCoordinate = useSelector(
    (state: IiotStoreState) => state.barView.selectedFloaterCoordinate
  );
  return (
    selectedFloaterCoordinate?.x === gridPosition?.x &&
    selectedFloaterCoordinate?.y === gridPosition?.y
  );
};
