import { useThree } from 'react-three-fiber';
import { useClippingPlane } from './useClippingPlane';
import { useLighting } from '../useLighting';
import { useMixerFameUpdate } from '../useFameMixerUpdate';

export const useInitializeBarView = () => {
  const three = useThree();
  useClippingPlane();
  useLighting(three.scene);
  useMixerFameUpdate();
}
