import { ActionCreator, applyMiddleware, createStore } from "redux";
import { IiotAction, IiotStoreState, rootReducer } from "./rootReducer";
import { composeWithDevTools } from "redux-devtools-extension";
import thunk, { ThunkAction, ThunkDispatch } from "redux-thunk";

export const store = createStore(
  rootReducer,
  composeWithDevTools(applyMiddleware(thunk))
);

export type IiotThunkAction<ReturnType> = ThunkAction<
  ReturnType,
  IiotStoreState,
  any,
  IiotAction
>;

export type IiotThunkActionCreator<ReturnType = void> = ActionCreator<
  IiotThunkAction<Promise<ReturnType>>
>;

export type IiotDispatch = ThunkDispatch<IiotStoreState, any, IiotAction>;
