import * as React from "react";
import { initialBarViewState } from "./data/barViewState";
import { Size } from "../utility/useSize";
import * as R from "ramda";
import { useLoadFloaterModel } from "./useLoadFloaterModel";
import { usePointCamera } from "./usePointCamera";
import { useInitializeBarView } from "./useInitializeBarView";
import { IiotStoreState } from "../rootReducer";
import { FloaterString } from "./FloaterString";
import { useEffect } from "react";

export const BarViewStateContext = React.createContext(initialBarViewState);

export const BarViewScene: React.FC<{
  size: Size;
  barView: IiotStoreState["barView"];
}> = props => {
  console.log("BarViewScene render");
  const barViewState = initialBarViewState;
  const floaterModel = useLoadFloaterModel();
  useInitializeBarView();
  usePointCamera(props.barView.installationStructure);
  useEffect(() => {
    barViewState.installationStructure = props.barView.installationStructure;
  }, [props.barView, barViewState]);
  return (
    <BarViewStateContext.Provider value={barViewState}>
      {floaterModel &&
        props.barView.installationStructure &&
        R.range(0, props.barView.installationStructure.y).map(index => (
          <FloaterString
            index={index}
            key={index}
          />
        ))}
    </BarViewStateContext.Provider>
  );
};
