import * as React from "react";
import { FloaterContainer } from "./FloaterContainer";
import * as R from "ramda";
import { BarViewStateContext } from "./BarViewScene";
import { useContext, useMemo } from "react";

export const FloaterString: React.FC<{
  index: number;
}> = props => {
  const state = useContext(BarViewStateContext);
  const floaterProps = useMemo(
    () =>
      R.range(0, state.installationStructure.x).map(xIndex => {
        return {
          key: `${xIndex}_${props.index}`,
          gridPosition: { x: xIndex, y: props.index }
        };
      }),
    [state.installationStructure, props.index]
  );
  return (
    <>
      {floaterProps.map(props => {
        return <FloaterContainer {...props} />;
      })}
    </>
  );
};
